.navbar {
  background-color: #fff;
  color: white;
  padding-bottom: 0px;
  padding-top: 0px;
  height: 110px;
}

.logo {
  height: 80px;
}

.nav-link {
  font-weight: bold;
  font-size: 20px;
  color: black;
}

.nav-link:hover {
  color: #30a6e6;
}

.nav-link:after {
  display: block;
  content: "";
  border-bottom: solid 3px black;
  transform: scaleX(0);
  transition: transform 250ms ease-in-out;
}

.nav-link:hover:after {
  transform: scaleX(1);
}

.nav-link.fromRight:after {
  transform-origin: 100% 50%;
}

.nav-link.fromLeft:after {
  transform-origin: 0% 50%;
}

.nav-menu {
  display: none;
}

/**
 RESPOSIVE
**/

@media screen and (max-width: 750px) {
  /* Rédigez vos propriétés CSS ici */
  .navbar-expand {
    display: none;
  }
}

@media screen and (max-width: 750px) {
  /* Rédigez vos propriétés CSS ici */
  .nav-menu {
    display: block;
    display: flex;
  }
}
